<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  props: {
    title: {},
    values: { default: [] },
    monthSelect: { default: true },
  },
  mounted() {
    this.$unlock({ monthSelect: this.monthSelect });
  },
  computed: {
    option() {
      let values = this.values;

      return {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)",
        },
        color: ["#00BEFF", "#FDB628", "#EA7148"],
        legend: {
          type: "scroll",
          bottom: 6,
          data: values.map((name) => name),
          pageTextStyle: {
            color: "#FFF",
          },
          textStyle: {
            color: "#FFF",
          },
        },
        title: {
          text: this.title,
          x: "center",
          top: "6%",
          textStyle: {
            fontSize: "100%",
            fontWeight: "normal",
            color: "#03D3D3",
          },
        },
        grid: {
          left: "6%",
          right: "4%",
          top: "30%",
          bottom: "10%",
          containLabel: true,
        },
        series: [
          {
            name: this.title,
            type: "pie",
            radius: ["40%", "50%"],
            center: ["50%", "50%"],
            data: values.sort(function (a, b) {
              return a.value - b.value;
            }),
            label: {
              color: "rgba(255, 255, 255, 1)",
              position: "center",
              formatter: function () {
                return "";
              },
              emphasis: {
                formatter: function (params) {
                  return (
                    "{p| " +
                    params.percent +
                    "%}" +
                    "\n{nm|" +
                    params.name +
                    "}"
                  );
                },
              },
              rich: {
                p: {
                  fontSize: 16,
                },
                nm: {
                  color: "#fff",
                  fontSize: 14,
                },
              },
            },
            labelLine: {
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              shadowBlur: 200,
              shadowColor: "rgba(0, 0, 0, 0.5)",
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function () {
              return Math.random() * 200;
            },
          },
        ],
      };
    },
  },
  components: {
    Echart,
  },
};
</script>